.Login {
  background-image: url("./images/bg.svg");
  height: 792px;
  display: block;
  /* position: absolute; */
  width: 100%;
}

.Login .bg-bottom {
  height: 100%;
  width: 100%;
  display: block;
  background: url(./images/bg_2.svg) center 470px no-repeat;
}

.Login .logo > img {
  padding: 15px;
  border-radius: 100%;
  margin: 0 auto;
  background-color: white;
  margin-bottom: 20px;
}

n .login-form {
  max-width: 300px;
}
.login-form-forgot {
  float: right;
}
.ant-col-rtl .login-form-forgot {
  float: left;
}
.login-form-button {
  width: 100%;
}

.Login #normal_login {
  margin: 0 auto;
  margin-top: 40px;
  width: 450px;
}

.Login #normal_login_username,
.Login #normal_login_password {
  padding: 10px 10px;
}

.Login .anticon {
  padding: 7px;
  background-color: #01a9ee;
  border-radius: 100%;
  color: white;
}

.Login .login-form-button {
  width: 85%;
  display: block;
  margin: 0 auto;
  border: 2px solid white;
  border-radius: 5px;
  height: 40px;
}

.Login .ant-row.ant-form-item {
  margin-bottom: 15px;
}

.Login .ant-input-affix-wrapper {
  margin-bottom: 25px;
}

.Login .ant-btn {
  background-color: #01a8ee;
}

.Login .ant-alert {
  margin-bottom: 3px;
}

.Login .anticon.anticon-exclamation-circle.ant-alert-icon {
  background-color: red;
}

.Login .cus-eye-pass {
  color: #666666;
  background-color: #ffffff;
}

.menu-cus .cus-menu-item {
  color: black;
  font-size: 24px;
  font-family: Roboto;
  text-decoration: none;
}

.menu-cus .anticon.anticon-shop,
.menu-cus .anticon.anticon-shopping-cart,
.menu-cus .anticon.anticon-user {
  font-size: 35px;
}
.ant-menu-item.ant-menu-item-only-child {
  padding: 0px 0px 0px 20px !important;
}

.ant-menu-item-selected {
  border: 1px solid #1890ff !important;
  border-right-width: 0px !important;
}
.menu-cus .ant-menu-submenu-title {
  font-size: 24px;
  color: black;
  margin-bottom: 5px;
}
.ant-menu-inline .ant-menu-item:after,
.ant-menu-vertical-left .ant-menu-item:after,
.ant-menu-vertical-right .ant-menu-item:after,
.ant-menu-vertical .ant-menu-item:after {
  border: none;
}

.menu-cus .onActive {
  color: #1890ff;
}

.line {
  margin-top: 10px;
  border-top: 1px solid #c1c1c1;
}

.ant-layout-content.site-layout-background.custom-content {
  padding: 0px !important;
}

.Shop .search {
  position: relative;
  top: -30px;
}

.clear {
  clear: both;
}

ul li {
  list-style: none;
  display: inline;
}

.ProducItem .name,
.ProducItem .image {
  cursor: pointer;
}

.ProducItem .name:hover,
.ProducItem .image:hover {
  opacity: 0.7;
}
.name {
  text-align: justify;
  max-height: 200px;
  margin: 10px 0 20px;
  font-size: 24px;
  font-weight: bold;
}

.price {
  margin: 10px 0 10px;
  font-size: 36px;
  font-weight: bold;
}

.DeatailProduct .image {
  margin-top: 20px;
}

.decription {
  text-align: justify;
  font-size: 24px;
  font-family: Roboto;
}

.DeatailProduct .cus-button {
  border-radius: 10px;
  font-size: 36px;
  font-family: Robato;
  padding: 10px 40px;
  margin-right: 20px;
  display: inline-block;
  height: auto;
}

.ant-layout {
  background-color: #ffffff;
}

.CartItem .image {
  margin-top: 20px;
}

.mg-bt {
  margin-bottom: 15px;
}

.CartItem .name:hover,
.CartItem .image:hover {
  opacity: 0.7;
}

.container-price-rate {
  float: right;
  border: 1px solid #c1c1c1;
  padding: 40px 20px;
  position: absolute;
  top: 40px;
  right: -50px;
  z-index: 1000;
  background-color: #ffffff;
}

.search_name {
  width: 300px;
  float: right;
  right: 40px;
}


.df {
  font-size: 24px;
  font-family: Robato;
  margin-bottom: 30px;
}

.ant-select-selector {
  border-width: 0 !important;
  font-size: 24px;
}

